.profile-info__title {
  max-width: 294px;
  font-family: "Inter", sans-serif;
  font-style: normal;
  font-weight: 500;
  font-size: 42px;
  line-height: 48px;
  color: #ffffff;
  margin: 0;
  text-overflow: ellipsis;
  white-space: nowrap;
  overflow: hidden;
}

@media screen and (max-width: 621px) {
  .profile-info__title {
    font-size: 27px;
    line-height: 33px;
    max-width: 189px;
  }
}
