.popup__container {
  background-color: white;
  min-width: 430px;
  border-radius: 10px;
  box-shadow: 0px 0px 25px rgba(0, 0, 0, 0.15);
  position: relative;
  transition: 0.5s;
}

@media screen and (max-width: 767px) {
  .popup__container {
    min-width: 282px;
    min-height: 322px;
  }
}
