.footer {
  max-width: 880px;
  margin: 67px auto 0;
  padding:0 40px 60px;
}

@media screen and (max-width: 767px) {
  .footer {
    max-width: 581px;
    margin: 48px auto 0;
    padding:0 0 36px;
  }
}

@media screen and (max-width: 622px) {
  .footer {
    max-width: 282px;
  }
}
